const body = document.querySelector('body');
const CatBox = document.querySelector('.CatBox');


body.addEventListener('mousemove', (e) =>{
    // console.log(e.pageX, e.pageY)

    let xAxis = (window.innerWidth / 2 - e.pageX) / 25
    let yAxis = (window.innerHeight / 2 - e.pageY) / 25

    CatBox.style.transform = `rotateY(${-xAxis}deg) rotateX(${yAxis}deg)`;
})


//Activate Laser
const Laser = document.getElementsByClassName('Laser');


body.addEventListener('mousedown', (e) =>{
    
    
    for(i = 0; i < Laser.length; i++){
        
        if(Laser[i].classList.contains('LaserActivate')){
            Laser[i].classList.remove('LaserActivate')
        }else{
            Laser[i].classList.add('LaserActivate')
        }
    }

})